/**
  * 
  * @param {String} name 
*/
 export const getParamsUrl = (name) => {
  //
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
  var r = window.location.search.substr(1).match(reg);
  if (r != null) return decodeURI(r[2]);
  return null;
}
/**
 * 
 * @param { fn } function  
 * @param { delay } number 
**/
export const debounce = (fn,t) => {
  let args = arguments // ES6  rest  args arguments,fnarguments
  let delay = t || 1000
  let timer = null //
  return function (){
    let th = this; // this
    // 
    if(timer){
      clearTimeout(timer) 
    }
    timer = setTimeout(function(){
      timer = null
      //
      fn.apply(th,args)
    },delay)
  }
}

/**
 * 
 * @param { fn } function 
 * @param { delay } number 
**/
export const throttle = (fn,t) => {
  let timer = null
  let delay = t || 500
  let startTime = Date.now()
  return function() {
    // 
    let curTime = Date.now()
    //  =  -  - 
    let interval = delay - (curTime - startTime)
    var th = this
    let args = arguments
    clearTimeout(timer)
    if(interval <= 0) {
      // 
      fn.apply(th, args)
      // 
      startTime = Date.now()
    } else {
      timer = setTimeout(()=>{
        fn.apply(th,args)
      }, delay)
    }
  }
}





